<template>
  <q-layout view="hHh lpR fFf">

 
    
    <q-header elevated :reveal="$q.screen.lt.sm" class="bg-primary">
      <q-toolbar :class="$q.screen.lt.md ? 'spaceBetween' : ''">

        <q-img 
            src="../assets/logo.png"
            spinner-color="white"
            width="145px"
            class="pointer scrollableTarget"
            @click="goHome"
          />  

        <div v-if="!$q.screen.lt.md"  :class="$q.screen.lt.sm ? 'colNav' : 'rowNav'">
          
          <!--Menu buttons-->
          <q-tabs align="left" style="max-width:85vw; "  mobile-arrows
            outside-arrows >
            <q-route-tab class="bg-primary" to="/" label="Inicio" />
            <q-route-tab class="bg-primary" to="/nosotros" label="Nosotros" />
            <q-route-tab class="bg-primary" to="/resultados" label="Resultados" />
            <q-route-tab class="bg-primary" to="/productos" label="Productos" />
            <q-route-tab class="bg-primary" to="/empleos" label="Trabaja con nosotros"  v-if="gStore.$state.jobSection"/>
          </q-tabs>


        </div>
        
        <q-btn v-else flat @click="drawerMenu = !drawerMenu" round dense icon="menu" />

      </q-toolbar>
      
    </q-header>

    <q-drawer
        v-model="drawerMenu"
        show-if-above
        :width="200"
        elevated
        behavior="mobile"
        class="bg-grey-3"
        
      >
        <q-scroll-area class="fit">
          <q-list>

            <q-img 
              src="../assets/logo.png"
              spinner-color="primary"
              style="margin: 10px; "
              width="180px"
            class="pointer"
            @click="goHome"

            />  
            <template v-for="(menuItem, index) in menuList" :key="index">
              <q-item clickable :active="menuItem.label === 'Outbox'" :to="menuItem.to" v-ripple>
                <q-item-section avatar>
                  <q-icon :name="menuItem.icon"  color="primary" />
                </q-item-section>
                <q-item-section style="font-size: 14px !important;">
                  {{ menuItem.label.toUpperCase() }}
                </q-item-section>
              </q-item>
              <q-separator :key="'sep' + index"  v-if="menuItem.separator" />
            </template>

          </q-list>
        </q-scroll-area>
      </q-drawer>



      <q-page-container  class="vh100">

          <router-view v-slot="{ Component }">
            <Transition :name="gStore.$state.transition" mode="out-in">
                <component :is="Component" />
            </Transition>
          </router-view>
          

          
          
      </q-page-container>

  <footerC/>
      
  
  </q-layout>


</template>

<script >
import { defineComponent } from 'vue' 
import footerC from '@/components/util/footerC.vue';
import { useGeneralStore } from '@/stores/generalStates';


export default defineComponent({
  name: 'MainLayout',
  components: { 
    footerC
  },
  data(){
    return{

      gStore: useGeneralStore(),

      drawerMenu: false,
      
       menuList: [
        {
          icon: 'fa-solid fa-home',
          label: 'INICIO',
          to: "/"
        },
        {
          icon: 'fa-solid fa-address-card',
          label: 'NOSOTROS',
          to: "/nosotros"
        },
        {
          icon: 'fa-solid fa-square-poll-vertical',
          label: 'RESULTADOS',
          to: "/resultados"
        },
        {
          icon: 'fa-brands fa-product-hunt',
          label: 'PRODUCTOS',
          to: "/productos"
        },
        {
          icon: 'fa-solid fa-suitcase',
          label: 'Trabaja con nosotros',
          to: "/empleos"
        },
        
      ]
    
    }
  },
  methods: {
    goHome(){
      this.$router.push("/")
    }
    
  },
  watch:{
    
  }, 
})
</script>

<style lang="scss" scoped>
 
 .q{
    &-toolbar{
      padding:10px;
    }

    &-tabs{
      padding:10px;
    }
  }

  .bg{
    &-primary{
     background-color: #294B9Aff !important; 
    }
  }


  .flex{
    display:flex;
  }

  .right{
      right: 0px;
    }
    .q-btn{
    font-size: 16px !important;
    text-decoration: none;
    transition: ease 0.3s;
  }
  .q-btn:hover{
    background-color: lighten($primary, 20%);
  }

  .spaceBetween{
    justify-content: space-between;
  }
  .q-toolbar{
    z-index: 99;
  }
  footer{
    z-index: 90;
  }
</style>
 